/**
 * 
 * @param time
 * @returns {{day: number, hour: *, minute: *, second: *}}
 */
export function timeTransform (time) {
  const day = Math.max(0, Math.floor(time / (1000 * 60 * 60 * 24)))
  const hour = Math.max(0, Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)))
  const minute = Math.max(0, Math.floor((time % (1000 * 60 * 60)) / (1000 * 60)))
  const second = Math.max(0, Math.floor((time % (1000 * 60)) / 1000))
  return {
    day: day,
    hour: hour,
    minute: minute < 10 ? '0' + minute : minute,
    second: second < 10 ? '0' + second : second
  }
}

/**
 * 
 * @returns {string}
 */
export function getDeviceType () {
  const ua = navigator.userAgent.toLowerCase()
  let type = ''
  if (window.WeixinJSBridge || /micromessenger/.test(ua)) {
    type = 'wx'
  }
  if (/qq\/([\d.]+)*/.test(ua)) {
    type = 'qq'
  }
  if (/jdapp/.test(ua)) {
    type = 'app'
  }
  if (/jdappershou/.test(ua)) {
    type = 'paipaiApp'
  }
  return type
}

/**
 * app
 *  http://npm.m.jd.com/package/@jmfe/jm-jdshare
 */
export function cornerShare (imgUrl) {
  let type = getDeviceType()
  let shareUrl = location.href
  if (shareUrl.indexOf('mine') !== -1 || shareUrl.indexOf('history') !== -1) {
    shareUrl = `${location.protocol}//${location.host}/c2c/star/`
  }
  let option = {
    title: '~',
    subTitle: '~',
    imageUrl: imgUrl || 'https://paipai.m.jd.com/c2c/image/pai-2x.png',
    shareUrl: shareUrl,
    type: 1,
    shareType: 0
  }
  if (shareUrl.indexOf('share') !== -1) {
    Object.assign(option, { title: '' })
  }

  if (type === 'paipaiApp') {
    location.href = `openapp.paipai://setAPPInfo/app?param=${encodeURIComponent(JSON.stringify(option))}`
  } else if (type === 'app') {
    jdshare.setShareInfo({
      title: option.title,
      content: option.subTitle,
      url: option.shareUrl,
      img: option.imageUrl,
      channel: 'Wxfriends,Wxmoments',
      callback: null, // 
      clickcallback: null, // 5.2  sendDirectShare 
      qrparam: null, //  5.2 
      timeline_title: '' // 5.4 
    })
  }
}

