/**
 * APP title
 * @param title
 */
export function setTitle (title) {
  if (title) {
    const para = { method: 'h5TitleEdit', params: { key1: title } }
    messageHandlers(para)
  }

  function messageHandlers (params) {
    const info = {
      version: '1.0.0',
      ua: navigator.userAgent,
      devicesType: getDevicesType()
    }
    if (info.devicesType === 1) {
      params.params = JSON.stringify(params.params)
      window.android.ppAppMessageHandler(encodeURIComponent(JSON.stringify(params)))
    } else if (info.devicesType === 2) {
      window.webkit.messageHandlers.ppAppMessageHandler.postMessage(params)
    }
    // 
    document.body.addEventListener('onpageshow', () => {
      if (info.devicesType === 1) {
        params.params = JSON.stringify(params.params)
        window.android.ppAppMessageHandler(encodeURIComponent(JSON.stringify(params)))
      } else if (info.devicesType === 2) {
        window.webkit.messageHandlers.ppAppMessageHandler.postMessage(params)
      }
    })
  }

  function getDevicesType () {
    let devicesType = 0
    const ua = navigator.userAgent
    const isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Linux') > -1 // androiduc
    const isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios
    if (isAndroid) {
      devicesType = 1
    } else if (isiOS) {
      devicesType = 2
    }
    return devicesType
  }
}

/**
 * 
 * @param forbid 
 */
const forbidTouchmove = function (e) { e.preventDefault() }
export function forbidScroll (forbid = false) {
  if (forbid) {
    document.body.addEventListener('touchmove', forbidTouchmove, { passive: false })
    document.body.style.overflow = 'hidden'
  } else {
    document.body.removeEventListener('touchmove', forbidTouchmove, { passive: false })
    document.body.style.overflow = 'auto'
  }
}

/**
 * 
 * @param show 
 */
export function toggleLoading (show) {
  let els = document.querySelectorAll('.myLoading')
  for (let i = 0; i < els.length; i++) {
    els[i].parentNode.removeChild(els[i])
  }
  if (!show) {
    return
  }
  let loadEl = document.createElement('div')
  loadEl.className = 'myLoading'
  loadEl.innerHTML = `
    <div style="background: url(data:image/gif;base64,) no-repeat;background-size: cover;"></div>
  `
  document.body.appendChild(loadEl)
}

/**
 * toast
 * @param text
 */
export function toast (text = '', time = 2000) {
  let els = document.querySelectorAll('.myToast')
  for (let i = 0; i < els.length; i++) {
    els[i].parentNode.removeChild(els[i])
  }
  let el = document.createElement('div')
  el.className = 'myToast'
  el.innerHTML = text
  document.body.appendChild(el)
  setTimeout(() => {
    el.style.transform = 'translate(-50%, 0)'
    el.style.opacity = 1
  }, 100)
  setTimeout(() => {
    el.style.transform = 'translate(-50%, 70%)'
    el.style.opacity = '0'
    setTimeout(() => { el.parentNode && el.parentNode.removeChild(el) }, 300)
  }, time)
}

/**
 *  https://cf.jd.com/pages/viewpage.action?pageId=79700612
 * @param type {pv: pvuv click}
 * @param param
 * pv -> {pageName: }
 * click -> {eventId: ID, event_param: click}
 */
export function logSend (param = {}, type = 'pv') {
  let setIt = () => {
    try {
      let MPing = window.MPing
      if (type === 'pv') {
        let pv = param.pageId ? new MPing.inputs.PV(param.pageId) : new MPing.inputs.PV()
        delete param.pageId
        Object.assign(pv, param)
        ;(new MPing()).send(pv)
      } else {
        let click = param.eventId ? new MPing.inputs.Click(param.eventId) : new MPing.inputs.Click()
        delete param.eventId
        Object.assign(click, param)
        click.updateEventSeries()
        ;(new MPing()).send(click)
      }
    } catch (err) {}
  }
  if (!document.querySelectorAll('script[src="//wl.jd.com/unify.min.js"]').length) {
    window.jap = {
      siteId: 'JA2018_4111189', // 
      autoLogPv: false, // pv
      anchorpvflag: false // 
    }
    let scri = document.createElement('script')
    scri.setAttribute('src', '//wl.jd.com/unify.min.js')
    scri.onload = () => { setIt() }
    document.head.appendChild(scri)
  } else {
    setIt()
  }
}

/**
 * get
 * @param url 
 * @param params 
 * @returns {Promise<any>} promise
 */
export function httpGet (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {params: params})
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * post
 * @param url 
 * @param params 
 * @param isJson json
 * @returns {Promise<any>} promise
 */
export function httpPost (url, params = {}, isJson = false) {
  if (!isJson) {
    // axiosjson(Content-Type: application/json)
    // Formdata(Content-Type: application/x-www-form-urlencoded)
    params = qs.stringify(params)
  }
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * cookieget
 * @param url 
 * @param params 
 * @returns {Promise<any>} promise
 */
export function httpGetWithCookie (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.create({})({
      url: url,
      method: 'get',
      params: params,
      withCredentials: true
    })
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * cookiepost
 * @param url 
 * @param params 
 * @param isJson json
 * @returns {Promise<any>} promise
 */
export function httpPostWithCookie (url, params = {}, isJson = false) {
  let config = {
    url: url,
    method: 'post',
    data: params,
    withCredentials: true
  }
  if (!isJson) {
    // axiosjson(Content-Type: application/json)
    // Formdata(Content-Type: application/x-www-form-urlencoded)
    Object.assign(config, {data: qs.stringify(params)})
  }
  return new Promise((resolve, reject) => {
    axios.create({})(config)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * 
 * @param options 
 * options: {
 *    url: 'http://localhost:4000/query', // 
 *    method: 'get', // 
 *    data: {a: 1, b: 2}, // post body
 *    params: {a: 1, b: 2}, // url
 *    withCredentials: true // cookie
 *    timeout: 1000, // 
 *    headers: { 'Content-Type': 'application/x-www-form-urlencoded'} // 
 *    onUploadProgress: function (progressEvent) {}, // 
 *    onDownloadProgress: function (progressEvent) {}, // 
 * }
 * @returns {Promise<any>} promise
 */
export function httpAjax (options) {
  return new Promise((resolve, reject) => {
    axios.create({})(options)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * jsonp
 * @param url 
 * @param params 
 * @returns {Promise<any>} promise
 */
export function httpJsonp (url, params = {}) {
  return new Promise((resolve, reject) => {
    jsonp(url + jsonToString(params), {
      timeout: 10000
      // name: 'callabck'
    }, (err, res) => {
      if (err) {
        reject(err)
      } else {
        return ((['0', '200'].indexOf(String(res.code)) !== -1) || (res.Identity && res.Identity.IsAuthenticated)) ? resolve(res) : reject(res)
      }
    })
  })
}

