/**
 * 4
 * https://cf.jd.com/pages/viewpage.action?pageId=81788288#id-0.%E7%94%A8%E6%88%B7%E7%9B%B8%E5%85%B3%E6%8E%A5%E5%8F%A3-2.1%E6%96%B9%E6%B3%95%E5%88%97%E8%A1%A8
 */

import {toMap, toMapArr, httpJsonp} from '@/assets/js/util'

const queryProvince = '//bizgw.jd.com/area/getProvinceList' // 
const queryCity = '//bizgw.jd.com/area/getCityListByProvinceId' // 
const queryCounty = '//bizgw.jd.com/area/getCountyListByCityId' // 
const queryTown = '//bizgw.jd.com/area/getTownListByCountyId' // 

const urlMap = {
  province: {url: queryProvince},
  city: {url: queryCity, param: 'provinceId'},
  county: {url: queryCounty, param: 'cityId'},
  town: {url: queryTown, param: 'countyId'}
}

const state = {
  province: [], // 
  city: [], // 
  county: [], // 
  town: [], // 
  prMap: {}, // map
  ciMap: {}, // map
  coMap: {}, // map
  diMap: {}, // map
  prCiMap: {}, // -map
  ciCoMap: {}, // -map
  coDiMap: {} // -map
}

/**
 * id
 * @param areaId id
 * @returns {*|Array}
 */
const areaArrMap = function (areaId) {
  let {prCiMap, ciCoMap, coDiMap} = state
  return prCiMap[areaId] || ciCoMap[areaId] || coDiMap[areaId] || []
}

/**
 * statemap
 */
const updateStateMap = function () {
  state.prMap = toMap(state.province, 'id')
  state.ciMap = toMap(state.city, 'id')
  state.coMap = toMap(state.county, 'id')
  state.diMap = toMap(state.town, 'id')
  state.prCiMap = toMapArr(state.city, 'pid')
  state.ciCoMap = toMapArr(state.county, 'pid')
  state.coDiMap = toMapArr(state.town, 'pid')
}

/**
 * state
 * @param payload
 */
const addData = function (payload) {
  if (payload.level) {
    // debugger
    state[payload.level] = [...state[payload.level], ...payload.data]
    updateStateMap()
  }
}

/**
 * id id(statestate)
 * @param areaId id [||] 
 * @returns {Promise<any>}
 */
const cacheArea = function (level, areaId) {
  return new Promise(async (resolve, reject) => {
    if (!level || level === 'province') { //  
      if (state.province.length === 0) { //  
        try {
          let res = await httpJsonp(queryProvince, {isHidden: 1})
          addData({level: 'province', data: res.data})
          resolve()
        } catch (err) {
          reject(err)
        }
      } else { //  
        resolve()
      }
    } else { //  
      if (!areaArrMap(areaId) || areaArrMap(areaId).length === 0) { //  
        try {
          let res = await httpJsonp(urlMap[level].url, {[urlMap[level].param]: areaId})
          res.data.forEach(e => (e.pid = areaId))
          addData({level: level, data: res.data})
          resolve()
        } catch (err) {
          reject(err)
        }
      } else { //  
        resolve()
      }
    }
  })
}

cacheArea() // 

export default {
  areaArrMap,
  cacheArea,
  state
}



