let jdPage = {}

const directive = {
  bind (el, binding, vnode) {
    // 400px
    const distance = vnode.data.attrs.distance || 400
    // 
    const callback = binding.value
    // 
    let allLoad = vnode.data.attrs.allLoad
    // 
    let isLoading = false
    // (window)
    let target = binding.modifiers.bindWindow ? window : el
    // 
    let loadNow = !binding.modifiers.loadNotNow
    // 
    let dpr = document.documentElement.getAttribute('data-dpr')

    // 
    async function loadIt () {
      isLoading = true
      let loadEl = addLoading(el)
      try {
        await callback()
      } catch (err) {}
      isLoading = false
      delLoading(el, loadEl)
    }

    target.onscroll = function (e) {
      if (isLoading || allLoad.is) {
        return
      }
      let scrollTarget = e.target.scrollingElement || e.target
      let scrollHeight = scrollTarget.scrollHeight
      let scrollTop = scrollTarget.scrollTop
      let height = window.screen.height * dpr

      if (scrollHeight - (scrollTop + height) <= distance) {
        loadIt().catch()
      }
    }

    if (loadNow) {
      loadIt().catch()
    }
  },
  unbind (el, binding, vnode) {
    (binding.modifiers.bindWindow ? window : el).onscroll = null
  }
}

function addLoading (el) {
  let loadEl = document.createElement('div')
  loadEl.className = 'pageLoading'
  loadEl.innerHTML = `
    <span>
      <i style="background: url(data:image/gif;base64,) no-repeat;background-size: cover;"></i>
      ...
    </span>
  `
  el.appendChild(loadEl)
  return loadEl
}

// 
function delLoading (el, loadEl) {
  el.removeChild(loadEl)
}

jdPage.install = function (Vue) {
  Vue.directive('jd-page', directive)
}

export default jdPage



