import {host, httpJsonp, toast, uuid} from '../../assets/js/util'

// 
const pollUrl = '//bizgw.jd.com/cms?ids=starbanner_show'
// 
const recommendUrl = '//bizgw.jd.com/operate/skusInfo'
//    cfhttps://cf.jd.com/pages/viewpage.action?pageId=132106459#id--
const goodsUrl = `${host.promotion}/collect_praise/index`

const state = {
  pollData: [], // 
  recommendData: [], // 
  goodsData: [] // 
}

const getters = {
}

const mutations = {
  setPollData (state, payload) { state.pollData = payload },
  setRecommendData (state, payload) { state.recommendData = payload },
  setGoodsData (state, payload) { state.goodsData = payload }
}

const actions = {
  /**
   * 
   */
  async getPollData ({commit}) {
    try {
      let res = await httpJsonp(pollUrl)
      commit('setPollData', (res.data || {starbanner_show: []}).starbanner_show || [])
    } catch (err) {}
  },
  /**
   * 
   */
  async getRecommendData ({commit}, payload) {
    try {
      let res = await httpJsonp(recommendUrl + '?operateCodes=' + payload)
      commit('setRecommendData', res.data.skusList[payload] || [])
    } catch (err) {}
  },
  /**
   * 
   */
  async clearRecommendData ({commit}) {
    try {
      commit('setRecommendData', [])
    } catch (err) {}
  },
  /**
   * 
   */
  async getGoodsData ({commit}) {
    try {
      let res = await httpJsonp(goodsUrl)
      // 
      if (res.serverTime) {
        let now = (new Date()).getTime()
        let deviation = now - res.serverTime
        res.result.forEach(e => {
          e.uuid = uuid()
          e.startTime = e.startTime + deviation
          e.endTime = e.endTime + deviation
        })
      }
      // 
      res.result.forEach(e => {
        e.detailImgs = (e.icons || []).concat(e.detailImgs || [])
      })
      commit('setGoodsData', res.result || [])
    } catch (err) {
      toast(err.message || '')
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



