import Vue from 'vue'
import Router from 'vue-router'

const routerData = [
  { path: '/', redirect: '/home' },
  // 
  { path: '/$guide$', component: () => import('../page/guide') },
  // 
  {
    path: '/home',
    component: () => import('../page/home'),
    meta: { title: '' }
  },
  // 
  {
    path: '/detail',
    component: () => import('../page/detail'),
    meta: { title: '' }
  },
  // 
  {
    path: '/share',
    component: () => import('../page/share'),
    meta: { title: '' }
  },
  // 
  {
    path: '/history',
    component: () => import('../page/history'),
    meta: { title: '' }
  },
  // 
  {
    path: '/mine',
    component: () => import('../page/mine'),
    meta: { title: '' }
  },
  // 
  {
    path: '/rule',
    component: () => import('../page/rule'),
    meta: { title: '' }
  }
]

Vue.use(Router)
export default new Router({
  base: '/c2c/star/',
  mode: 'history',
  routes: routerData
})



